html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding:0;
  font-family: 'Public Sans', sans-serif;
  color: $grayishBlue;
  line-height: 1.3;
}

a, a:visited, a:hover {
  text-decoration: none;
  color: $grayishBlue;

}

//flexbox styles
.flex {
  display: flex;
  &-jc-sb {
    justify-content: space-between;
  }

  &-jc-c {
    justify-content: center;
  }

  &-ai-c {
    align-items:center;
  }
}

